    getUserRecords(usrId, cltvtId) {
      var myPlantRecordJson;
      var recrdDttm; 
      var cltvtActCd;
      var cntnts;
      this.myRecordsArr = null;
      
      console.log('usrId : ', usrId, ' cltvtId : ', cltvtId);

      axios
        .post("/api/plant", { contentsId: 16, usrId: usrId, cltvtId: cltvtId })
        // axios.post(this.router, resultJsonImp)
        .then((response) => {
          myPlantRecordJson = response.data;
          console.log('myPlantRecordJson ::: ', myPlantRecordJson);
          this.myPlantRecordArr = myPlantRecordJson[0].get_result_json;
          this.myPlantRecordArrPage = myPlantRecordJson[0].get_result_json;
          var myRecordsJsonTmp = {};
          var myRecordsJsonArrayTmp = [];
          // var page_num = 2;

          this.pNumTotal = Math.ceil(this.myPlantRecordArr[0].rslt_cnt / 5);

          for (var i in this.myPlantRecordArr) {
            if (this.myPlantRecordArr[i].cltvt_id == cltvtId 
              // && ((page_num-1)*5+1 <= this.myPlantRecordArr[i].rn && this.myPlantRecordArr[i].rn <= page_num*5) 
                    ) {
              recrdDttm = this.myPlantRecordArr[i].recrd_dttm;
              cltvtActCd = this.myPlantRecordArr[i].cltvt_act_cd;
              cntnts = this.myPlantRecordArr[i].cntnts;

              myRecordsJsonTmp = {
                recrdDttm: recrdDttm,
                cltvtActCd: cltvtActCd,
                cntnts: cntnts,
              };
              myRecordsJsonArrayTmp.push(myRecordsJsonTmp);
              this.myRecordsArr = myRecordsJsonArrayTmp;
            }
          }
          // console.log('this.myRecords length :::: ', this.myRecords.length);
        })
        .catch((error) => {
          console.log(error);
        });
    },
    getMyPlantDiary(pCltvtId, count){
      this.imgUrlArr = [];
      const config = {
            method: "POST",
            url: "/api/plantShowImage",
            responseType: "json",
            data: { cltvtId: pCltvtId, pageNumber: count },
        };

      var fileArr = []; 
      var imgDiaryCntnts;
      var respJson;
      var buffer;
      var recrd_dttm; 
      var daysSince;
      var daysSinceSow;
      var fileName;
      var reactCnt;

      axios(config)
        .then((response) => {
          respJson = response.data;

          for(var i in respJson) {
            fileArr[i] = respJson[i].file;
            imgDiaryCntnts = respJson[i].imgDiaryCntnts;
            recrd_dttm = respJson[i].recrd_dttm;
            daysSince = respJson[i].days_since;
            daysSinceSow = respJson[i].days_since_sow;
            fileName = respJson[i].fileName;
            reactCnt = respJson[i].reactCnt;

            ///// base64 -> ArrayBuffer -> Blob -> ObjectUrl
            // 1. get buffer(file obj)
            buffer = fileArr[i].data;  // filesArr :  {type: 'Buffer', data: Array(515059)}
            
            // 2. buffer to blob
            const blob = new Blob([new Uint8Array(buffer)]);

            // 3. blob -> ObjectUrl
            const fileObjectUrl = window.URL.createObjectURL(blob); //revokeObjectURL???

            this.imgUrlArr[i] = {
                                cltvt_id:respJson[i].cltvt_id
                                , cltvt_act_cd:respJson[i].cltvt_act_cd
                                , recrd_dttm:recrd_dttm
                                , daysSince:daysSince
                                , daysSinceSow:daysSinceSow
                                , fileName: fileName
                                , fileObjectUrl:fileObjectUrl
                                , imgDiaryCntnts:imgDiaryCntnts
                                , reactCnt:reactCnt
                              };
          }
        this.diaryCnt = this.imgUrlArr.length;

        })
        .catch((error) => {
          console.log(error);
        });
    },
