                recrdDttm : this.myPlantRecordArr[i].recrd_dttm, 
                cltvtActCd : this.myPlantRecordArr[i].cltvt_act_cd, 
                cntnts : this.myPlantRecordArr[i].cntnts
              };
              myRecordsJsonArrayTmp.push(myRecordsJsonTmp);
              this.myRecordsArr = myRecordsJsonArrayTmp;
            }
      }
