  <div class="contents_container_inner">
    <div class="contents_item_left">
      <div style="display: flex; flex-direction: column; align-items: center;">
        <button class="button_category" v-on:click="showStory('soil');">흙</button>
        <button class="button_category" v-on:click="showStory('light');">빛</button>
        <button class="button_category" v-on:click="showStory('temp');">온도</button>
        storyGbn : {{ this.storyGbn }}
      </div>
    </div>
    <div class="contents_item_right" v-if="storyIntro">
      설명
    </div>
    <!-- {{ storyList }} -->
    <div class="contents_item_right" v-else >
      <div class="contents_container_inner" 
            style="width: 70rem; height: 50rem; float: left;
                  overflow-y: scroll;">
        <div v-for="(upperItem, idx) in storyList" :key="idx">
          <div v-if="upperItem.story == storyGbn">
            <!-- <div v-for="(item, id) in dataStorySoil" :key="id" style="display: flex; justify-content: col; align-items: center;"> -->
