              <!-- <div v-bind:id="item.title" class="contents_title" style="align-items: center;"> -->
              <div v-bind:id="item.title" class="contents_title">
                {{ item.title }}
                <!-- <div v-on:click="showExam = !showExam;" class="contents_add">+</div> -->
