              <!-- <div class="contents_container_inner" v-show="showExam"> -->
              <div class="contents_container_inner">
                <div class="contents_item_right" style="border: 0px solid #9abf7f;">
                  <div class="board_contents" v-html="item.contents.split('\n').join('<br />')" ></div>
                </div>
              </div>
            </div>
          </div>
        </div>

        <!-- 책갈피 -->
        <div style="width: 12rem; margin-left: 85rem; margin-top: 1rem; position: fixed;
                  border-left: 1px solid lightgray;">
          <div v-for="(upperItem, idx) in storyList" :key="idx">
            <div v-if="upperItem.story == storyGbn">
              <div v-for="(item, id) in upperItem.contents" :key="id" style="display: flex; flex-direction: column; flex-wrap: wrap;">
                <!-- <a v-bind:href='"#"+item.title'>{{item.title}}</a> -->
                <div class="right_side_btn"><a v-bind:href='"#"+item.title'>{{item.title}}</a></div>
                <!-- "`https://${item.url}`" -->
