                <!-- <button class="right_side_btn" v-scroll-to="'#때알구조2ssssss'">{{item.title}}</button> -->
                
                <!-- <a v-bind:href='"#"+item.title'><button class="right_side_btn">{{item.title}}</button></a> -->
                <!-- <button id="test" class="right_side_btn" v-on:click="moveToTitle(item.title);">{{item.title}}</button> -->
                <!-- <button id="test" class="right_side_btn" v-on:click="moveToTitle(item.title);">{{item.title}}</button> -->
                <!-- <button id="test" class="right_side_btn" v-on:click="moveToTitle(item.title);">{{item.title}}</button> -->
                <!-- <div v-bind:id="item.title" class="right_side_btn" v-on:click="moveToTitle(item.title);">{{item.title}}</div> -->
                
                

                <!-- <a v-bind:href='"#"+item.title'><div class="right_side_btn">{{item.title}}</div></a> -->
                <!-- </a> -->

                <!-- <a v-bind:href='"#"+item.title'> -->
                  <!-- <div class="right_side_btn" style="text-overflow: ellipsis; overflow: hidden; white-space: nowrap; max-width: 150px;">
                    <h3>{{ item.title }}</h3>
                  </div> -->
                  <!-- <button class="right_side_btn">{{item.title}}</button> -->
                <!-- </a> -->
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import BarChart from '../Charts/BarChart'
import storySoilJson from './json/plantKnowledgeSoil.json'
import storyLightJson from './json/plantKnowledgeLight.json'
import storyTempJson from './json/plantKnowledgeTemp.json'

const axios = require("axios");

export default {
  // name: 'App',
  components: { 
          // BarChart, 
          // LineChart 
        },
  data() {
    
    return {
      dataStorySoil: storySoilJson,
      dataStoryLight: storyLightJson,
      dataStoryTemp: storyTempJson,
      
      cropJson: null,
      sidoJson: null,
      sigunguCd: '108',   // default 서울
      cropChecked:[1], // default

      // chart
      // chartData: {
      //     labels: [ 'January', 'February', 'March' ],
      //     datasets: [ { data: [40, 20, 12] } ]
      // },
      loaded: false,
      chartData: null,
      targetType: 'A',
      storyIntro: true,
      storyGbn: 'soil',
      storyList: [{story: "soil", contents: storySoilJson},
                  {story: "light", contents: storyLightJson},
                  {story: "temp", contents: storyTempJson}],

      // showExam: true, show and hide test
    }
  },
  created() {
  },
  mounted() {
    // this.getSidoList();
    // this.getCropList();
    // this.getWeather();
  },
  methods:{
    moveToTitle(paramTitle) {
      location.href="#"+paramTitle;
      // document.getElementById('test').focus();
      document.getElementById(paramTitle).focus();
      // console.log('paramTitle', paramTitle);
    },
    showStory(paramStoryGbn) {
      this.storyGbn = paramStoryGbn;
      this.storyIntro = false;
    },
    showExplain(targetType) {
      this.targetType=targetType;
    },  
    async getSidoList(){
      var resultJson = {};
      this.loaded = false;
      const axios = require("axios");
      await axios
        .post("/api/lineChartDb", { contentsId: 19})
        .then((response) => {
          resultJson = response.data;
          this.sidoJson = resultJson[0].get_result_json;
          this.resultKey = Object.keys(this.sidoJson[0]);  // sido, sinungu
        })
        .catch((error) => {
          console.log(error);
        });
    },

    async getCropList(){
      // crop list
      var resultJson = {};
      await axios
          .post("/api/lineChartDb", {contentsId: 17})
          .then((response) => {
            resultJson = response.data;
            this.cropJson = resultJson[0].get_result_json;
          })
          .catch((error) => {
          console.log(error);
          });
    },

    async getWeather(){
      var resultJson = {};
      var crpIdList = '';
      crpIdList = '\'';

      for (var i in this.cropChecked) {
            if (this.cropChecked[i]) {
              console.log(this.cropChecked[i]);
              crpIdList = crpIdList + '' + this.cropChecked[i] + ',';
            }
          }
      crpIdList = crpIdList.slice(0, -1);
      crpIdList = crpIdList + '\'';
      
      this.loaded = false;
      const axios = require("axios");
      await axios
        .post("/api/lineChartDb", { contentsId: 18, sigungu_cd: this.sigunguCd, crp_id_list: crpIdList })
        .then((response) => {
          resultJson = response.data[0].get_result_json;

          this.chartData = { labels: resultJson.lables, datasets: resultJson.datasets };
          this.chartOptions = { responsive: true, maintainAspectRatio: true };

          this.loaded = true;
        })
        .catch((error) => {
          console.log(error);
        });
    }
  }
}
</script>
