<template>
    <img :src="require(`@/assets/images/common/div_fill.jpg`)">  
    <div class="contents_title">
      식물이야기 게시글
    </div>
    <div class="contents_container_inner" style="margin: auto;  display: flex;   justify-content: center;   align-items: center;">
      <h5>listMode : {{this.listMode}} / viewMode : {{this.viewMode}} / writeMode : {{this.writeMode}} / editMode : {{this.editMode}}</h5>
    </div>
    <div class="contents_container_inner">
      <!-- 조회form -->
      <div class="contents_item_data" v-if="viewMode">
        <div class="" style="width: 100%; margin-top: 1rem;
                              border-top: 3px solid lightgray; border-bottom: 1px solid lightgray">
          <div style="width: 85%; padding: 1rem; float:left; color: #61656A; font-weight: bold;">{{ healingActivity.title }}</div>
          <div style="padding: 1rem;">{{ healingActivity.reg_dt }}</div>
        </div>
        <div class="" style="width: 100%; border-bottom: 1px solid lightgray;">
          <div class="board_contents" 
                style="padding-top: 1rem; padding-bottom: 1rem; padding-left: 2rem; padding-right: 2rem;" 
                v-html="healingActivity.cntnts.split('\n').join('<br />')" ></div>
        </div>
      </div>
    </div>
    <div class="contents_container_inner" v-if="listMode">
      <div class="keyword_list">
        <!-- <button class="button_keyword_item" 
                v-for="item in this.tagList" 
                v-bind:key="item.id" 
                v-on:click="this.contentsList(item.id, item.tagValue);">
          #{{ item.tagName }} 
        </button> -->
        <div class="button_keyword_item" 
                v-for="item in this.tagList" 
                v-bind:key="item.id" 
                v-on:click="this.contentsList(item.id, item.tagValue);">
          #{{ item.tagName }} 
        </div>
      </div>
    </div>
    <div class="contents_container_inner">
      <!-- 목록form -->
      <div class="contents_item_data">
        <div style="width: 100%; display: flex; flex-direction: column; justify-content: center; align-items: center;">
          <table style="width: 100%;">
            <tr style="text-align: center">
              <th>No.</th><th>제목</th><th>작성자</th><th>날짜</th><th>조회</th>
            </tr>
            <!-- <tr style="width:100%;" v-for="(healingActivity,id) in dataHealingActivity" :key="id" > -->
            <tr class="tr_content" style="width:100%;" v-for="(item, id) in displayedPosts" :key="id">
              <td style="width:7%; text-align: center;">
                <div v-if="this.healingActivity.brd_id == item.brd_id" style="font-weight: bold;">▶▶</div>
                <div v-else>{{ item.brd_id }}</div>
              </td>
              <td v-on:click="showContent(item);">
                {{ item.title }}
              </td>
              <td style="width:12%; text-align: center;">
                관리자
              </td>
              <td style="width:10%; text-align: center;">
                {{ item.reg_dt }}
              </td>
              <td style="width:7%; text-align: center;">
                0
              </td>
            </tr>
          </table>
        </div>
      </div>
      <div class="contents_item_data" style="height: 0rem;">
        <div style="width: 100%; display: flex; justify-content : center; width: 20rem;">
          <!-- <div style="border:1px solid black; display: inline-block;">가운데정렬하시라고</div> -->
          <div style="width:40%; display: flex; justify-content: center; align-items: center; margin:auto;">
            <button class="button_basic" v-if="currentPage > 1" v-on:click="currentPage--">이전페이지</button>
          </div>
          <div style="width:20%; display: flex; justify-content: center; align-items: center; margin:auto;">
            <span>{{ currentPage }} / {{ totalPages }}</span>
          </div>
          <div style="width:40%; display: flex; justify-content: center; align-items: center; margin:auto;">
            <button class="button_basic" v-if="currentPage < totalPages" v-on:click="currentPage++">다음페이지</button>
          </div>
        </div>
      </div>
      <div class="contents_item_data" style="height: 0rem;">
        <div style="margin-left: 90%;">
          <!-- <button v-on:click="writeMode=true; healingActivity.title=null; healingActivity.cntnts=null;" v-if="!this.writeMode">글쓰기</button> -->
          <button class="button_basic" v-on:click="showList();">목록</button>
        </div>
      </div>
    </div>

    <!-- <div class="contents_container_inner">
      <div class="contents_item_data" v-if="viewMode">
        <button class="button_basic" v-if="viewMode">수정</button>
        <button class="button_basic" v-if="editMode" v-on:click="doDml('update');">수정</button>
        <button class="button_basic" v-on:click="doDml('delete');">삭제</button>
        <button class="button_basic" v-on:click="showList();">목록</button>
      </div>
    </div> -->


</template>
  
<script>
const axios = require("axios");
import healingActivityJson from '../../assets/json/healingActivityJson.json'; // select get_result_json(9);
// const dataHealingActivityJson = healingActivityJson;
const healingActivity = {};

  export default {
    props: [], 
    components: {
    },
    data() {
      return {
        inputTag: "",
        tagId: "0",
        tagList: [
          { id: 0, tagName: "전체", tagValue: undefined},
          { id: 1, tagName: "친환경", tagValue: "친환경"},
          { id: 2, tagName: "재활용재사용", tagValue: "재활용재사용"},
          { id: 3, tagName: "비용절감", tagValue: "비용절감"},
          { id: 4, tagName: "반려식물", tagValue: "반려식물"},
          { id: 5, tagName: "흙", tagValue: "흙"},
          { id: 6, tagName: "빛", tagValue: "빛"},
          { id: 7, tagName: "온", tagValue: "온"},
        ],
        listMode: true,
        viewMode: false,
        writeMode: false,
        editMode: false,
        title: "",
        contents: "",
        regDt: "", 
        resultArr: [],
        // resultKey: [],
        img: "@/assets/images/plant1_01.jpg",





        dataHealingActivity: healingActivityJson,
        healingActivity,
        showContentYn: false,
        currentPage: 1,
        postsPerPage: 20,
      };
    },
    computed: {
      displayedPosts() {
        const startIndex = (this.currentPage - 1) * this.postsPerPage;
        const endIndex = startIndex + this.postsPerPage;
        return this.dataHealingActivity.slice(startIndex, endIndex);
      },
      totalPages() {
        return Math.ceil(this.dataHealingActivity.length / this.postsPerPage);
      }
    },
    created() {
    },
    mounted() {
      this.contentsList();
    },
    update() {
    },
    methods: {
      contentsList(id, tagName) {
        console.log('id : ', id, ' tagName : ', tagName);
        var contentsId = null;
        if (id == 0 || id == undefined) {
          contentsId = 9;   // 전체 게시글 가져오기
        } else {
          contentsId = 13;  // 선택한 키워드 관련글들 가져오기
        }
        // console.log(contentsId, ' ', tagName);

        this.listMode = true;
        this.viewMode = false;
        this.writeMode = false;
        this.editMode = false;
        
        axios
          // .post("/api/boardMas", { dml: '', contentsId: "9" })
          .post("/api/board", { dml: '', contentsId: contentsId, tag: tagName })
          // axios.post(this.router, resultJsonImp)
          .then((response) => {
            this.resultJson = response.data;
            this.resultArr = this.resultJson[0].get_result_json;
            // this.resultKey = Object.keys(this.resultArr[0]);
            // delete this.resultKey[2];
            // this.insert = false;
            this.update = false;
          })
          .catch((error) => {
            console.log(error);
          });


        // // get from json
        // axios
        //   .get("/api/json/test", {
        //     responseType: 'json', // JSON 형식으로 응답 데이터 받기
        //   })
        //   .then((response) => {
        //     this.resultJson = response.data;
        //     this.resultArr = this.resultJson[0].get_result_json;
        //     console.log('get from json : this.resultArr : ', this.resultArr);
        //   })
        //   .catch((error) => {
        //     console.log(error);
        //   });




        
        this.brdId = null;
        // this.insert = false;
        this.update = false;
    },
    viewData(brdId) {
      console.log('row : ', brdId);
      this.listMode = false;
      this.viewMode = true;
      this.writeMode = false;
      this.editMode = false;

      var resultJson;
      var resultArr;
      // var resultKey;

      const axios = require("axios");
      axios
        // .post("/api/board", { dml: '', brdId: brdId })
        .post("/api/board", { dml: '', contentsId: 9, brdId: brdId })
        .then((response) => {
          resultJson = response.data;
          resultArr = resultJson[0].get_result_json;
          // this.resultKey = Object.keys(resultArr[0]);
          
          this.title = resultArr[0].title;
          this.contents = resultArr[0].cntnts;
          this.regDt = resultArr[0].reg_dt;
          console.log('this.regDt :: ', this.regDt);
          
          this.brdId = brdId;
          // console.log('this.title = ', this.title);
        })
        .catch((error) => {
          console.log(error);
        });

        // if(수정권한있으면(작성자,관리자)) 
        // this.editMode=true;
        // else 
        //   this.viewMode=true;
    },
    doDml(dml) {
      console.log('healingActivity ::: ', this.healingActivity);
      if ( this.healingActivity.title.length > 0 && this.healingActivity.cntnts.length > 0) {
        
        var resultMsg;
        const axios = require("axios");
        axios
          .post("/api/board", { dml: dml, brdId: this.healingActivity.brd_id, title: this.healingActivity.title, contents: this.healingActivity.cntnts})
          .then((response) => {
            // console.log('response.data : ',response.data.result);
            resultMsg = response.data.result;
            console.log('resultMsg : ',resultMsg);
              
            // alert(resultMsg);
            this.editMode = false;
            this.listMode = true;
            this.contentsList();
          })
          .catch((error) => {
            console.log(error);
          });   
      } else {
        alert("제목과 내용 입력은 필수입니다.");
      }
    },

    showContent(healingActivity) {
      this.healingActivity = healingActivity;
      this.showContentYn = true;

      this.listMode = true;
      this.viewMode = true;
      this.writeMode = false;
      this.editMode = false;

      document.body.scrollTop = 0; // For Safari
      document.documentElement.scrollTop = 0; // For Chrome, Firefox, IE and Opera

      // if(수정권한있으면(작성자,관리자))
      // this.viewMode = false;
      // this.editMode=true; // 임시 테스트
      // else 
      //   this.viewMode=true;
    },
    hideContent() {
      this.showContentYn = false;
    },
    showList() {
      this.writeMode = false; 
      this.editMode = false; 
      this.listMode = true; 
      this.showContentYn = false;
      this.viewMode = false;

      document.body.scrollTop = 0; // For Safari
      document.documentElement.scrollTop = 0; // For Chrome, Firefox, IE and Opera
    }
    },
  };
  </script>